<template>
  <div class="edit-container">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="副标题" prop="title">
        <el-input v-model="ruleForm.title"></el-input>
      </el-form-item>
      <el-form-item label="商品描述" prop="desc">
        <el-input type="textarea" v-model="ruleForm.desc"></el-input>
      </el-form-item>
      <el-form-item label="商品类目" prop="category">
        <el-select
          v-model="ruleForm.category.one"
          placeholder=""
          @change="handleId(ruleForm.category.one)"
          clearable
        >
          <el-option
            :label="item.name"
            :value="item.id"
            v-for="item in list"
            :key="item.id"
          ></el-option>
        </el-select>
        <el-select v-model="ruleForm.category.two" placeholder="" clearable>
          <el-option
            :label="item"
            :value="item"
            v-for="item in c_items.c_items"
            :key="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="商品标签" prop="tags">
        <el-input v-model="ruleForm.tags"></el-input>
      </el-form-item>
      <el-form-item label="商品售价" prop="price">
        <el-input v-model="ruleForm.price"></el-input>
      </el-form-item>
      <el-form-item label="商品折扣价" prop="price_off">
        <el-input v-model="ruleForm.price_off"></el-input>
      </el-form-item>
      <el-form-item label="商品库存" prop="inventory">
        <el-input v-model="ruleForm.inventory"></el-input>
      </el-form-item>
      <el-form-item label="计量单位" prop="unit">
        <el-input v-model="ruleForm.unit"></el-input>
      </el-form-item>
      <el-form-item label="商品相册" prop="img">
        <el-upload
          :action="
            'https://mallapi.duyiedu.com/upload/images?appkey=' +
            $store.state.user.appkey
          "
          list-type="picture-card"
          :on-success="success"
          :on-remove="remove"
          name="avatar"
          :file-list="ruleForm.img"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>
      <el-form-item prop="status">
        <el-checkbox v-model="ruleForm.status">是否上架</el-checkbox>
      </el-form-item>
      <el-form-item>
        <el-button class="btn" type="primary" @click="submitForm()"
          >完成</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from "@/api/product.js";
export default {
  data() {
    return {
      ruleForm: {
        title: "",
        desc: "",
        category: {
          one: "",
          two: "",
        },
        tags: "",
        price: "",
        price_off: "",
        inventory: "",
        unit: "",
        img: [],
        status: true,
      },
      rules: {
        title: [
          { required: true, message: "请输入商品副标题", trigger: "blur" },
        ],
        tags: [{ required: true, message: "请输入商品标签", trigger: "blur" }],
        price: [
          { required: true, message: "请输入商品售价", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              value = parseFloat(value);
              // console.log(isNaN(value))
              if (isNaN(value)) {
                callback(new Error("请输入数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        price_off: [
          { required: true, message: "请输入商品折扣价", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              value = parseFloat(value);
              const priceValue =
                this.ruleForm.price !== ""
                  ? parseFloat(this.ruleForm.price)
                  : 0;
              if (priceValue == 0) {
                callback(new Error("请填写原价"));
              } else if (value > priceValue) {
                callback(new Error("折扣不正确"));
              } else if (isNaN(value)) {
                callback(new Error("请输入数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        inventory: [
          { required: true, message: "请输入商品库存", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              value = parseFloat(value);
              if (isNaN(value)) {
                callback(new Error("请输入数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        unit: [
          { required: true, message: "请输入商品计量单位", trigger: "blur" },
        ],
      },
      c_items: [],
      imgList: [],
      list: [],
    };
  },
  methods: {
    handleId(id) {
      // console.log(this.ruleForm.category.one)
      this.c_items = this.list[id - 1];
      this.ruleForm.category.two = "";
      if (!this.c_items) {
        this.c_items = [];
      }
    },
    success(response, file, fileList) {
      if (response.status == "success") {
        // console.log(file,fileList)
        this.imgList = [];
        for (let i = 0; i < fileList.length; i++) {
          this.imgList.push(fileList[i].url);
        }
      }
    },
    remove(file, fileList) {
      // console.log(file, fileList)
      this.imgList = [];
      for (let i = 0; i < fileList.length; i++) {
        this.imgList.push(fileList[i].url);
      }
    },
    submitForm() {
      alert('没用奥！！！！！')
      let _this = this;
      let num;
      let obj = {
        title: this.ruleForm.title,
        desc: this.ruleForm.desc,
        // category: this.ruleForm.category.one,
        category: (function(){
          if( typeof _this.ruleForm.category.one == 'number') {
            return _this.ruleForm.category.one
          }
          _this.list.map(item=>{
            if(item.name == _this.ruleForm.category.one) {
              return num = item.id
            }
          })
          return num
        })(),
        c_items: [this.ruleForm.category.two],
        tags: this.ruleForm.tags.split(","),
        price: this.ruleForm.price,
        price_off: this.ruleForm.price_off,
        unit: this.ruleForm.unit,
        images: this.imgList,
        status: this.ruleForm.status ? 1 : 0,
        inventory: this.ruleForm.inventory,
        sale: this.$store.state.editData.sale,
        _id: this.$store.state.editData.id,
        // id: "1253"
      };
      // console.log(this.$store.state.editData)
      // console.log(obj)
      api.edit(obj).then((res) => {       
        if(res.status == 'success') {
          // console.log(res);
          this.$router.push({name: 'productList'})
        }
      });
    },
    // category	Number	必须	商品类目
  },
  created() {
    api.categoryList().then((res) => {
    if(this.$store.state.editData.title == undefined) {
      this.list = res.data.data;
      return ;
    }
    this.list = res.data.data;
    this.c_items = this.list[this.$store.state.editData.category].c_items;
    console.log(this.c_items)
    this.ruleForm.title = this.$store.state.editData.title;
    this.ruleForm.desc = this.$store.state.editData.desc;
    this.ruleForm.category.one = this.list[this.$store.state.editData.category].name;
    this.ruleForm.category.two = this.$store.state.editData.c_item;
    this.ruleForm.tags = this.$store.state.editData.tags
      ? this.$store.state.editData.tags.toString()
      : "";
    this.ruleForm.price = this.$store.state.editData.price;
    this.ruleForm.price_off = this.$store.state.editData.price_off;
    this.ruleForm.inventory = this.$store.state.editData.inventory;
    this.ruleForm.unit = this.$store.state.editData.unit;
    this.ruleForm.img = this.$store.state.editData.images
      ? this.$store.state.editData.images.map((item) => {
          return { url: item };
        })
      : [];
    this.ruleForm.status = this.$store.state.editData.status == 1 ? true : false;
    // console.log(this.ruleForm)
    });
  },
};
</script>

<style scoped lang="less">
.edit-container {
  width: 50%;
  margin-top: 50px;
  margin: 50px auto 0;
  el-form {
    margin: 0 auto;
  }
  .btn {
    width: 100%;
  }
}
</style>